	module.exports = {"data":{"markdownRemark":{"id":"C:\\Users\\IronMan\\Desktop\\projects\\blog\\src\\pages\\blog\\2016-04-16-无恒产者无恒心.MD absPath of file >>> MarkdownRemark","html":"<p>睡前抄诗经的时候，脑海里突然浮现出前段看孟子里面的那句“无恒产者无恒心”。</p>\n<p>按正常的解释来讲，这句话是说人没有财产积蓄 就容易违心干坏事儿。</p>\n<p>在此时，却又想到，恒产并不一定是物质财产，应是包含物质乃至精神学识方面的财产。</p>\n<p>人有安身立命的学识，有足够的底蕴，心正，意诚，其次有恒产者，即使无财产积蓄，也不会乱生事端。</p>\n<p>恒产，就像坚实的大地。无恒产，就像漫步于云端，不知道自己的落脚点是不是空的，是不是陷 阱，于是便只能向那些看起来坚实的地方行进， 这样的地方可以说便是利益的聚集地。\n无恒产者， 恐惧于自己在云端漫步时的无处下脚，便只能去逐利，并为了逐利可以付出极大的代价。\n因为不逐利，他们便不知该向哪里迈步，该何去何从。</p>\n<p>而有恒产者，每一步都走的安然，知道自己的追求，所以每一步都走的安稳。</p>","frontmatter":{"title":"无恒产者无恒心","date":"04月 16日, 2016年"}}},"pathContext":{"slug":"/blog/2016-04-16-无恒产者无恒心/","previous":{"id":"C:\\Users\\IronMan\\Desktop\\projects\\blog\\src\\pages\\blog\\2016-04-01-愚人节.MD absPath of file >>> MarkdownRemark","fields":{"slug":"/blog/2016-04-01-愚人节/"},"frontmatter":{"title":"愚人节","tags":["感悟","反思"],"date":"2016-04-01T00:00:00.000Z","cover":null}},"next":{"id":"C:\\Users\\IronMan\\Desktop\\projects\\blog\\src\\pages\\blog\\2016-05-23-写写自己.MD absPath of file >>> MarkdownRemark","fields":{"slug":"/blog/2016-05-23-写写自己/"},"frontmatter":{"title":"写写自己","tags":["回忆","反思"],"date":"2016-05-23T00:00:00.000Z","cover":null}}}}

